<template>
    <li>
      <label>
        <input type="checkbox" :checked="todosObj.done" @change="handleCheck(todosObj.id)"/>
        <span>{{ todosObj.title }}</span>
      </label>
      <button class="btn btn-danger" @click="deleteT(todosObj.id)">删除</button>
    </li>
</template>
<script>
export default {
  name: 'MyItem',
  props:['todosObj','checkTodo','deleteTodo'],
  methods:{
    handleCheck(id){
      this.checkTodo(id)
    },
    deleteT(id){
      //deleteT(id) 获取当前 todos.id
      //confirm()提示框 按确认就返回true 按取消就返回false
      if (confirm('确认删除吗')) {
        this.deleteTodo(id)
      }
    }
  }
}
</script>
<style>
/*item*/
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 1px solid #ddd;
}

li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li button {
  float: right;
  margin-top: 3px;
  display: none;
}

li:hover{
  background-color: #ddd;
}

li:hover button{
  display: block;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}
</style>