<template>
  <div id="app">
    <div id="root">
      <div class="todo-container">
        <div class="todo-wrap">
          <MyHeader @receive="receive"/>
          <MyList :todos="todos" :checkTodo="checkTodo" :deleteTodo="deleteTodo" />
          <MyFooter :todos="todos" @checkedAllTodo="checkedAllTodo" @deleteAllTodo="deleteAllTodo"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MyHeader from "./components/MyHeader.vue"
import MyList from "./components/MyList.vue"
import MyFooter from "./components/MyFooter.vue"

export default {
  name: 'App',
  components: {
    MyHeader,
    MyList,
    MyFooter
  },
  data(){
    return{
      todos: JSON.parse(localStorage.getItem('todos')) || []
    }
  },
  methods:{
    //添加一个todos对象
    receive(x){
      this.todos.push(x)
    },
    checkTodo(id){
      //遍历todos对象
      this.todos.forEach((todo) => {
        //判断从MyItem传过来的id是否与当前遍历的id相同
        if(id == todo.id){
          //相同取反
          todo.done = !todo.done
        }
      });
    },
    //删除一个todos对象
    deleteTodo(id){
      //deleteTodo(id) 获取想要删除对象的id
      //filter() 过滤掉 todo.id == id 对象
      this.todos = this.todos.filter((todo) => {
        return todo.id !== id
      })
    },
    //全选
    checkedAllTodo(done){
      //1.遍历数组
      //2.将每一条的数据的done值改成传过来的done值
      this.todos.forEach((todo)=>{
        todo.done = done
      })
    },
    //删除以完成
    deleteAllTodo(){
      //过滤掉当前done值为true的数据
      this.todos = this.todos.filter((todo) => {
        return !todo.done
      })
    }
  },
  watch:{
    //默认监视只能监视最外层
    todos:{
      deep:true,//深度监视
      handler(value){
        //当todos发生变化 就将最新的 todos(value)传个本地存储
        localStorage.setItem('todos',JSON.stringify(value))
      }
    }
  }
}
</script>

