<template>
    <div class="todo-footer">
        <label>
          <input type="checkbox" v-model="isAll"/>
        </label>
        <span>
          <span>已完成{{ doneTotal }}</span> / 全部{{ total }}
        </span>
        <button class="btn btn-danger" @click="deleteAll">清除已完成任务</button>
    </div>
</template>
<script>

export default {
  name: 'MyFooter',
  props:['todos'],
  computed:{
    //全部 => 获取todos数组的长度
    total(){
      return this.todos.length
    },
    //已完成
    doneTotal(){
      // reduce(值1,值2)循环数组
      // 值1:函数 (pre,current) => {}
      // pre => 值2 || current => 返回当前对象
      // 值2:初始值
      return this.todos.reduce((pre,current)=>{
        //如果当前 current.done(todos.done) == true  (pre+1)
        return pre + (current.done ? 1 : 0)
      },0)
    },
    //全选 or 取消全选
    isAll:{
      get(){
        //判断 total(所有数据的总数) 是否全等于 doneTotal(以完成的总数)
        //以及 total(所有数据的总数) 是否大于 0
        return this.total === this.doneTotal && this.total > 0
      },
      set(value){
        //1.获取当前 input 的 checked值
        //2.将获取到的值传给 APP组件上的 checkedAllTodo方法
        // this.checkedAllTodo(value)
        this.$emit('checkedAllTodo',value)
      }
    },
  },
  methods:{
    //调用APP deleteAllTodo 方法
    deleteAll(){
      // this.deleteAllTodo()
      this.$emit('deleteAllTodo')
    }
  }
}
</script>
<style>
/*footer*/
.todo-footer {
  height: 40px;
  line-height: 40px;
  padding-left: 6px;
  margin-top: 5px;
}

.todo-footer label {
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}

.todo-footer label input {
  position: relative;
  top: -1px;
  vertical-align: middle;
  margin-right: 5px;
}

.todo-footer button {
  float: right;
  margin-top: 5px;
}
</style>